.social-media-reels-pointers-main-container {
  padding: 5vh 3vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #e5e5e5;
  gap: 2vh;
}

.social-media-reels-pointers-main-container h1 {
  text-align: center;
  margin-bottom: 2vh;
}

.social-media-reels-pointers-main-container h3 {
  text-align: left;
  background-color: #612d91;
  padding: 5vh 3vw;
  color: #fff;
  border-radius: 25px;
}

.social-media-reels-pointers-animations {
  position: relative;
  animation: social-media-reels-slide-in 1s ease-in forwards;
  opacity: 0;
}

@keyframes social-media-reels-slide-in {
  0% {
    right: -120%; /* Initial position outside the screen */
    opacity: 0;
  }
  100% {
    right: 0; /* Final position on the screen */
    opacity: 100;
  }
}
